<template>
  <div class="home">
    <div class="personalTop">
      <div class="topTitle">
        个人中心
      </div>
      <div class="topUser" >  
        <van-image
          :src="userInfo.wechat_img"
          class="userImg leftEl"
          round
        >
        <template v-slot:error>加载失败</template>
        </van-image> 
        <div class="userInfo leftEl">
          <div class="userName">{{userInfo.nick_name}}</div>
          <div class="userPhone">{{userInfo.rank_name}}</div>
          <div class="userPhone">推荐人:{{userInfo.pid_member.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')}}</div>
        </div>

        <span class="rightEl"  >
            <img src="../../assets/imgs/personal/setting.png" alt="" class="userSet" @click='toSet' >
            <div style="margin-top:10px;" @click='toUser'>
        点击编辑资料
          <van-icon name="arrow" />
     </div>
        
        </span>
      </div>
      <div class="topBtm">
          <van-grid :border="false" :column-num="4">
                 <van-grid-item>
                  {{userInfo.total_direct || 0}}
                  <div class="topText">直推人数</div>
              </van-grid-item>
              <van-grid-item to="myCredit">
                  {{userInfo.totalIntegral.toFixed(1)   }}
                  <div class="topText">我的积分</div>
              </van-grid-item>
              <van-grid-item to="addCredit">
                  {{userInfo.recharge_integral | numFilter }}
                  <div class="topText">充值积分</div>
              </van-grid-item>
                
               <van-grid-item to="pekCredit">
                  {{userInfo.integral  | numFilter}}
                  <div class="topText">奖励积分</div>
              </van-grid-item>
            </van-grid>
      </div>
    </div>
    <!-- 订单 -->
    <div class="myOrder">
      <div class="orderTop" @click="toAll">
        <img src="../../assets/imgs/personal/order.png" alt="" />
        <span class="orderTitle">我的订单</span>
        <span class="rightEl">
          全部订单
          <van-icon name="arrow" />
        </span>
      </div>
      <div class="orderBtm">
        <van-grid :border="false" :column-num="4">
          <!-- <van-grid-item to="../myOrder?type=0">
            <img src="../../assets/imgs/personal/unPay.png" alt="" />
            <p>待付款</p>
            <span class="info"  v-if="orderNum.nums>0">{{orderNum.nums}}</span>
          </van-grid-item> -->
          <van-grid-item to="../myOrder?type=0">
            <img src="../../assets/imgs/personal/unPay.png" alt="" />
            <p>待发货</p>
            <span class="info"  v-if="orderNum.nums2>0">{{orderNum.nums2}}</span>
          </van-grid-item>
          <van-grid-item to="../myOrder?type=1">
            <img src="../../assets/imgs/personal/unSend.png" alt="" />
            <p>待收货</p>
            <span class="info"  v-if="orderNum.nums3>0">{{orderNum.nums3}}</span>
          </van-grid-item>
          <!-- <van-grid-item to="../myOrder?type=1" >
            <img src="../../assets/imgs/personal/cancel.png" alt="" />
            <p>已取消</p>
            <span class="info"  v-if="orderNum.nums1>0">{{orderNum.nums1}}</span>
          </van-grid-item> -->
          <van-grid-item to="../myOrder?type=2">
            <img src="../../assets/imgs/personal/unGet.png" alt="" />
            <p>已完成</p>
            <span class="info" v-if="orderNum.nums4>0">{{orderNum.nums4}}</span>
          </van-grid-item>
            <van-grid-item to="../myOrder?type=3">
            <img src="../../assets/imgs/personal/done.png" alt="" />
            <p> 全部</p>
            <span class="info" v-if="orderNum.nums4>0">{{orderNum.nums4}}</span>
          </van-grid-item>
        </van-grid>
      </div>
    </div>
    <!-- 分栏 -->
    <van-cell title="我的推广" is-link to="myTeam">
      <div slot="icon">
        <img src="../../assets//imgs/personal/team.png" alt="" class="team"/>
      </div>
    </van-cell>
    <!-- <van-cell title="邀请码" is-link to="myInvite">
      <div slot="icon">
        <img src="../../assets//imgs/personal/invite.png" alt="" class="invite"/>
      </div>
    </van-cell> -->
    <van-cell title="地址管理" is-link to="myAddress">
      <div slot="icon">
        <img src="../../assets//imgs/personal/location.png" alt="" class="location"/>
      </div>
    </van-cell>
    <van-cell title="积分转账" is-link to="changeweek">
      <div slot="icon">
        <img src="../../assets//imgs/personal/图层 21@2x.png" alt="" class="servicer"/>
      </div>
    </van-cell>
      <van-cell title="收款管理" is-link to="wefair">
      <div slot="icon">
        <img src="../../assets//imgs/personal/图层 20@2x.png" alt="" class="service"/>
      </div>
    </van-cell>
    <!-- <a href="http://p.qiao.baidu.com/cps/chat?siteId=14304149&userId=28908574"> -->
      <!-- <van-cell title="联系客服" is-link to="myService">
      <div slot="icon">
        <img src="../../assets//imgs/personal/service.png" alt="" class="service"/>
      </div>
    </van-cell> -->
    <!-- </a> -->
      <van-cell title="使用方法" is-link to="playwef">
      <div slot="icon">
        <img src="../../assets//imgs/personal/图层 23@2x.png" alt="" class="servicet"/>
      </div>
    </van-cell>
      <van-cell title="帮助中心" is-link to="helpweek">
      <div slot="icon">
        <img src="../../assets//imgs/personal/图层 22@2x.png" alt="" class="service"/>
      </div>
    </van-cell>
    
  </div>
</template>

<script>

import {userCenter} from "@/api/personal"
// import {orderNums} from "@/api/order"
export default {
  data() {
    return {
      userInfo:{},
      orderNum:{}
    };
  },
  filters: {
    // 版本号显示12位，超过12位显示...
    ellipsis:function(value) {
      if (!value) return "";
      if (value.length > 15) {
        return value.slice(0, 15) + "...";
      }
      return value;
    }
  },
 created(){
     this.getUser();
    //  orderNums().then(res=>{
    //      console.log(res)
    //   this.orderNum=res;
    //  })
 },
  methods: {
    getUser(){
      userCenter().then(res=>{
         this.userInfo=res;
      })
    },
    toUser(){
      this.$router.push('userInfo');
    },
       toSet(){
      this.$router.push('setting');
    },
    toAll(){
      this.$router.push({path:'myOrder'});
    },
    goUrl(url){
      this.$router.push(url);
    }
  }
};
</script>
<style lang="less" scoped>
/deep/ .van-grid-item{
          overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
}
a{
    display: block;
}
  .home{
    padding-bottom:100px;
  }
.personalTop {
  width: 100%;
  background: url(../../assets/imgs/personal/topBg.png) 0 0 no-repeat;
  background-size: 100% 100%;
  height: 220px;
  box-sizing: border-box;
  padding: 17px 15px 15px;
  font-size: 18px;
  color: white;
  .userSet{
      width: 30px;
      height: 30px;
      position: relative;
      left: 40px;
  }
  .topTitle {
    text-align: center;
  }
  .topUser {
    overflow: hidden;
    .userImg {
      width: 72px;
      height: 72px;
      margin-right: 11px;
      margin-top: 11px;
    }
    .userInfo {
      color: white;
      padding-top: 10px;
      .userName {
        font-size: 20px;
      }
      .userPhone {
        font-size: 13px;
        margin-top: 2px;
      }
    }
    .rightEl {
      margin-top: 5px;
      color: white;

      font-size: 12px;
    }
  }
  .topBtm {
    margin-top: 13.5px;
    width: 100%;
    text-align: center;
    .topText {
      font-size: 15px;
      margin-top: 6.5px;
    }
    /deep/ .van-grid-item__content{
      background: transparent;
      color: white;

      padding-top:6px;
    }
  }
}
.myOrder {
  width: 355px;
  margin: 11px auto 6.5px;
  padding-top: 14px;
  box-shadow: 0 0 8px 2px #eee;
  color: #151d28;
  .orderTop {
    display: flex;
    align-items: center;
    margin: 0 5px;
    padding-bottom: 9px;
    border-bottom: 1px solid #e5e5e5;
    img {
      width: 15px;
      height: 18px;
      margin-right: 14px;
    }
    .orderTitle {
      font-size: 14px;
    }
    .rightEl {
      color: #666;
      font-size: 12px;
      margin-left: 55%;
    }
  }
  .orderBtm {
    padding-top: 21px;
    /deep/ .van-grid-item__content {
      padding: 0;
    }
    img {
      width: 20px;
      height: 17px;
      position:relative;
      display: inline-block;
    }
    span{
      position: absolute;
      top:-10px;
      right:10px;
      display: inline-block;
      color:#fff;
      background: #de4f53;
      font-size:12px;
      width:20px;
      text-align: center;
      border-radius: 50%;
      height:20px;
      line-height: 20px;
    }
    p {
      font-size: 12px;
      color: #151d28;
    }
  }
}
.team{
  width:25px;
  height: 21px;
  margin-right:24px;
}
.invite{
  width:22px;
  height: 21px;
  margin-right:25px;
}
.location{
  width:21px;
  height: 25px;
  margin-right:26px;
}
.service{
  width:24px;
  height: 26px;
  margin-right:24px;
}
.servicer{
  width:24px;
  height: 24px;
  margin-right:24px;
}
.servicet{
     width:22px;
  height: 24px;
  margin-right:24px;
}
</style>
